$turquoise: #0ad9c6;
$dark: #1f1f1f;
$font: "Open Sans",
"Arial",
sans-serif;

body {
  margin: 0;
}

/* open-sans-regular */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src:
    url("../fonts/open-sans-regular.woff2") format("woff2"),
    url("../fonts/open-sans--regular.woff") format("woff");
}

/* open-sans-bold */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src:
    url("../fonts/open-sans-bold.woff2") format("woff2"),
    url("../fonts/open-sans-bold.woff") format("woff");
}

/* open-sans-300 */
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 300;
  src:
    url('../fonts/open-sans-300.woff2') format('woff2'),
    url('../fonts/open-sans-300.woff') format('woff');
}
